<template>
    <div id="input_dlg">
        <div class="dimmed"></div>
        <div class="ly_pop ly_medium" style="z-index:1500;">
            <div class="content_wrap">
                 <span style="color:blue;">'{{project.name}}'</span> ?<br>    .<br><span style="font-weight:bold;color:red;"> !</span>
            </div>
            <div class="btn_box">
                <button type="button" class="btn_cancel btn_b" v-on:click="removeProject()"></button>
                <button type="button" class="btn_confirm btn_b" v-on:click="closeDlg()"></button>
            </div>
        </div>
    </div>
</template>

<script>
    import gEventBus from './../service/gEventBus';

    export default {
        props : {
            project: {
                type: Object,
                default: {}
            }
        },
        data : function() {
            return {
            }
        },
        created : function () {
            gEventBus.$on('REMOVE_PROJECT', this.onRemoveProject);
        },
        beforeDestroy : function () {
            gEventBus.$off('REMOVE_PROJECT');
        },
        mounted : function () {
        },
        methods : {
            closeDlg: function () {
                this.$emit('destroy');
                this.$destroy();
            },
            removeProject: function () {
                if (this.project) {
                    this.$store.dispatch('REMOVE_PROJECT', {name: this.project.name, uuid: this.project.uuid});
                }
            },
            onRemoveProject: function(result) {
                if(result) {
                    this.closeDlg();
                } else {
                    alert("  .");
                }
            }
        }
    }
</script>

